/**
 * Typography
 */
html {
  font-size: 20px;
}

body {
  font: normal normal 300 1em/1.5 "Neuton", serif;
  color: #4e4e56;
  text-rendering: optimizeLegibility;
}

@media only screen and (min-width: 40em) {
  body {
    font-size: 1.25em;
  }
}

/**
 * @section Headings
 */
h1,
h2 {
  font-family: "Lato", sans-serif;
  font-weight: 400;
}

h1 {
  font-size: 1.953125em;
}

h2 {
  font-size: 1.5625em;
}

/**
 * @section Inline
 */
p {
  margin: 0 0 1em;
  text-align: justify;
}

a {
  text-decoration: none;
  word-wrap: break-word;
  color: #93c893;
}

a:link {
  color: #93c893;
}

a:visited,
a:hover,
a:active,
a:focus {
  outline: none;
  color: #689c81;
}

a:hover {
  color: #689c81;
}

/**
 * Animations
 */
.main-header {
  animation: fadeIn 0.5s forwards 0.5s ease-in, fadeOut 0.5s forwards 6s ease-out;
  /*   animation: fadeIn 0.5s forwards 0.5s ease-in; */
}

.text {
  animation: stroke 6s infinite linear;
}

.gravatar {
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-delay: 6.5s;
  animation-timing-function: ease-in;
  animation-name: bounceInUp;
  animation-delay: 6.5s;
}

.name {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

.occupation {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

.biography {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

.twitter {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

.medium {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

.codepen {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

.github {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

.email {
  animation: fadeIn 0.5s forwards 6.75s ease-in;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: -250;
  }
}

@keyframes bounceInUp {
  from, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  from {
    opacity: 0;
    transform: translate3d(0, 300px, 0);
  }
  70% {
    opacity: 1;
    transform: translate3d(0, -20px, 0);
  }
  90% {
    transform: translate3d(0, 5px, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

/**
 * Modules
 */
/**
 * Greeting
 */
.main-header {
  opacity: 0;
  width: 20em;
  height: 6em;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -3em 0 0 -10em;
}

.main-header .greeting {
  font-size: 3.8146972656em;
  line-height: 1;
  text-align: center;
  margin: 0;
}

@media only screen and (min-width: 40em) {
  .main-header .greeting {
    font-size: 5.9604644775em;
  }
}

.main-header .text {
  fill: none;
  stroke-width: 2;
  stroke-linejoin: round;
  stroke-dasharray: 40 210;
  stroke-dashoffset: 0;
}

.main-header .text:nth-child(5n + 1) {
  stroke: #fb8d6f;
  animation-delay: -1.2s;
}

.main-header .text:nth-child(5n + 2) {
  stroke: #93c893;
  animation-delay: -2.4s;
}

.main-header .text:nth-child(5n + 3) {
  stroke: #e2d59d;
  animation-delay: -3.6s;
}

.main-header .text:nth-child(5n + 4) {
  stroke: #689c81;
  animation-delay: -4.8s;
}

.main-header .text:nth-child(5n + 5) {
  stroke: #d3124c;
  animation-delay: -6s;
}

/**
 * Layout
 */
html,
body {
  width: 100%;
  height: 100%;
  position: relative;
}

.container {
  width: 90%;
  height: 100%;
  max-width: 28em;
  margin: 0 auto;
}

.main-content {
  text-align: center;
  padding: 3em 0 0;
}

.social li {
  list-style: none;
}

.gravatar {
  border-radius: 12em;
  width: 6em;
  height: 6em;
}

@media only screen and (min-width: 40em) {
  .gravatar {
    width: 7.2em;
    height: 7.2em;
  }
}

.gravatar,
.name,
.occupation,
.biography,
.twitter,
.medium,
.codepen,
.github,
.email {
  opacity: 0;
}

